@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@900&display=swap);
body{height:100%;margin:0;background:#000}p{margin:0}#container{font-family:'Nunito Sans', sans-serif;display:grid;grid-template-areas:"header header header header header header" "menu main main main main main"}.info{grid-area:header}.titulo,.player,.inicio{margin:0px;padding:20px}.inicio{display:grid;grid-template-columns:auto}@media (min-width: 600px){.inicio{grid-template-columns:auto auto}}.texto_inicio{color:#fff;font-size:50px}.titulo{color:#fff;font-size:60px}@media (min-width: 600px){.titulo{font-size:80px}}.player{color:#fff;font-size:30px}@media (min-width: 600px){.player{font-size:45px}}.menu{grid-area:menu;font-size:21px}@media (min-width: 600px){.menu{font-size:45px}}.contenido{grid-area:main;text-align:justify;margin:0px;padding:20px}.select_color{color:red}ul{list-style-type:none;margin:0;padding:0;overflow:hidden}li{padding:20px}a{color:white}#si_no{width:200px;height:200px;border-radius:30px;display:flex;justify-content:center;align-content:center;flex-direction:column;text-align:center;font-size:60px}.botones_linea{overflow:hidden}.m_cuad{float:left;width:60px;height:60px;border-radius:15px;margin:6px}.m_cuad_nrmal{background-color:white}.m_cuad_selected{background-color:red}.inactive_si_no{background-color:white;color:black}.active_si_no{background-color:red;color:white}.sliders{width:60%}

input[type="range"]{height:25px;-webkit-appearance:none;margin:10px 0;width:100%}input[type="range"]:focus{outline:none}input[type="range"]::-webkit-slider-runnable-track{width:100%;height:5px;cursor:pointer;box-shadow:0px 0px 0px #000000;background:#ffffff;border-radius:7px;border:0px solid #ffffff}input[type="range"]::-webkit-slider-thumb{box-shadow:0px 0px 0px #000000;border:1px solid #ff0000;height:18px;width:18px;border-radius:25px;background:#ff0000;cursor:pointer;-webkit-appearance:none;margin-top:-7px}input[type="range"]:focus::-webkit-slider-runnable-track{background:#ffffff}input[type="range"]::-moz-range-track{width:100%;height:5px;cursor:pointer;box-shadow:0px 0px 0px #000000;background:#ffffff;border-radius:7px;border:0px solid #ffffff}input[type="range"]::-moz-range-thumb{box-shadow:0px 0px 0px #000000;border:1px solid #ff0000;height:18px;width:18px;border-radius:25px;background:#ff0000;cursor:pointer}input[type="range"]::-ms-track{width:100%;height:5px;cursor:pointer;background:transparent;border-color:transparent;color:transparent}input[type="range"]::-ms-fill-lower{background:#ffffff;border:0px solid #ffffff;border-radius:14px;box-shadow:0px 0px 0px #000000}input[type="range"]::-ms-fill-upper{background:#ffffff;border:0px solid #ffffff;border-radius:14px;box-shadow:0px 0px 0px #000000}input[type="range"]::-ms-thumb{margin-top:1px;box-shadow:0px 0px 0px #000000;border:1px solid #ff0000;height:18px;width:18px;border-radius:25px;background:#ff0000;cursor:pointer}input[type="range"]:focus::-ms-fill-lower{background:#ffffff}input[type="range"]:focus::-ms-fill-upper{background:#ffffff}

